import { Button, Form, Input, message, Modal, Select } from "antd";
import { FORM_FIELDS } from "constants/AddDomainFormConstant";
import { ADD_TEXT, CHILDREN, CONFIRM_MESSAGE, SUBMIT_TEXT } from "constants/CommonConstants";
import useIntlMessage from "hooks/useIntlMessage";
import { useState } from "react";
import { useIntl } from "react-intl";
import domainService from "services/DomainService";
import {
  ADD_SUB_DOMAIN,
  ADD_SUB_DOMAIN_MESSAGE,
  CANCEL_TEXT,
  DOMAIN_FORM_LAYOUT,
  DOMAIN_FORM_NAME,
  DOMAIN_MODAL_ADD_BUTTON_TYPE,
  DOMAIN_MODAL_BACK_BUTTON,
  SUB_DOMAIN_SUCCESS_MESSAGE,
  VALIDATION_FAILED,
} from "../../../../constants/AddSubDomainConstants";
const { Option } = Select;

const AddSubDomainForm = ({ setIsVisible, domainsList, onCancel, onSave }) => {
  const [domain, setDomainName] = useState(null);
  const [form] = Form.useForm();
  const getMessage = useIntlMessage();
  const intl = useIntl();
  const { confirm } = Modal;
  const { DOMAIN_NAME } = FORM_FIELDS;

  const onCreate = async data => {
    const res = await domainService.setPost(data);
    if (res) {
      setDomainName(res);
      success();
      onSave();
    }
  };

  const handleConfirm = () => {
    confirm({
      title: CONFIRM_MESSAGE.value,
      centered: true,
      onOk() {
        onCancel();
      },
    });
  };

  const Cancel = () => {
    if (form.isFieldsTouched()) {
      handleConfirm();
    } else {
      onCancel();
    }
  };

  const success = () => {
    message.success(SUB_DOMAIN_SUCCESS_MESSAGE.value);
  };

  const handleOk = () => {
    form
      .validateFields()
      .then(value => {
        form.resetFields();
        onCreate({
          domainId: value?.domainName,
          subDomainName: value?.subDomainName,
        });
        onCancel();
      })
      .catch(info => {
        console.log(VALIDATION_FAILED.value, info);
      });
  };

  const handleOnChange = e => {
    setDomainName(e.target.id, e.target.value);
  };

  return (
    <Modal
      visible={setIsVisible}
      title={ADD_SUB_DOMAIN.text}
      onCancel={Cancel}
      onOk={handleOk}
      footer={[
        <Button key={DOMAIN_MODAL_BACK_BUTTON.value} onClick={Cancel}>
          {CANCEL_TEXT.text}
        </Button>,
        <Button key={SUBMIT_TEXT.value} onClick={handleOk} type={DOMAIN_MODAL_ADD_BUTTON_TYPE.value}>
          {ADD_TEXT.text}
        </Button>,
      ]}
    >
      <Form form={form} labelAlign="left" layout={DOMAIN_FORM_LAYOUT.text} name={DOMAIN_FORM_NAME.text} onFinish={handleOk}>
        <Form.Item
          name={DOMAIN_NAME.value}
          label={getMessage(DOMAIN_NAME.label)}
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Select placeholder={getMessage(DOMAIN_NAME.placeholder)} showArrow showSearch optionFilterProp={CHILDREN}>
            {domainsList?.map(item => {
              return (
                <Option key={item?.domainId} value={item?.domainId}>
                  {item?.domainName}
                </Option>
              );
            })}
          </Select>
        </Form.Item>
        <Form.Item
          name={ADD_SUB_DOMAIN.value}
          label={ADD_SUB_DOMAIN.text}
          rules={[
            {
              required: true,
              message: ADD_SUB_DOMAIN_MESSAGE.value,
            },
          ]}
        >
          <Input
            placeholder={intl.formatMessage({ id: ADD_SUB_DOMAIN.placeholder })}
            onChange={handleOnChange}
            value={domain}
          />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default AddSubDomainForm;
