import { EditOutlined, PlusCircleOutlined, SearchOutlined, SyncOutlined, FilterOutlined } from "@ant-design/icons";
import useBreakpoint from "antd/lib/grid/hooks/useBreakpoint";
import { Button, Col, PageHeader, Radio, Row, Tooltip } from "antd";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import RestrictedContainer from "components/util-components/RestrictedContainer";
import { MANAGE_DOMAINS_TITLE } from "constants/AddSubDomainConstants";
import {
  ACTION,
  ACTIVE_STATUS,
  ALL_STATUS,
  INACTIVE_STATUS,
  LIGHT_NAVY_BLUE_COLOR,
  NAVIGATION_ASSETS,
  NAVIGATION_ASSETS_RIGHTS,
  SCREEN_ITEMS_TYPES,
  SORT_DIRECTIONS,
} from "constants/CommonConstants";
import useAuthRights from "hooks/useAuthRights";
import { useEffect, useRef, useState } from "react";
import { useDispatch } from "react-redux";
import { getSubDomains, setDomains } from "redux/actions";
import domainService from "services/DomainService";
import Utils from "utils";
import AddDomainForm from "./AddDomainForm";
import AddSubDomainForm from "./AddSubDomain";
import useIntlMessage from "hooks/useIntlMessage";
import { DOMAIN_FIELDS_NAME, DOMAIN_TABLE_COLUMNS } from "constants/ManageDomainsConstant";
import { showLoader } from "redux/actions/Loader";
import domainsFile from "assets/data/domain.xls";
import UploadData from "../../../../components/UploadData/index";

const ManageDomains = () => {
  let searchInput = useRef(null);
  const [searchText, setSearchText] = useState("");
  const [searchedColumn, setsearchedColumn] = useState("");
  const [domainStatus, setDomainStatus] = useState(ACTIVE_STATUS.value);
  const [filteredInfo, setFilteredInfo] = useState({});
  const [show, setShow] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [sortedInfo, setSortedInfo] = useState({});
  const [showUploadModal, setShowUploadModal] = useState(false);
  const [showAddDomainModal, setShowAddDomainModal] = useState(false);
  const [editDomainData, setEditDomainData] = useState(null);

  const [currentPage, setCurrentPage] = useState(1);
  const [currentPageSize, setCurrentPageSize] = useState(10);
  //  const domainList = useSelector(state => state.myTeams.domainList);
  const [domainsList, setDomainsList] = useState([]);
  const dispatch = useDispatch();
  const { hasAssetsRights } = useAuthRights();
  const isMobile = !Utils.getBreakPoint(useBreakpoint()).includes("sm");
  const getMessage = useIntlMessage();

  useEffect(() => {
    getDomainData(domainStatus);
  }, [domainStatus]);

  const AddSubDomain = () => {
    setShow(true);
  };

  const AddDomain = () => {
    setShowAddDomainModal(true);
    setIsEdit(false);
  };

  const Upload = () => {
    setShowUploadModal(true);
  };
  const getDomainData = async status => {
    try {
      dispatch(showLoader(true));
      const response = await domainService.getAllDomains({ status });
      if (response) {
        const flatDomainsList = domainService.flatDomainList(response);
        setDomainsList(flatDomainsList);
        dispatch(setDomains(response));
      }
    } catch (e) {
      console.error("Error in getting the domain information", e);
    } finally {
      dispatch(showLoader(false));
    }
  };

  const onChangeStatus = e => {
    setDomainStatus(e.target.value);
    setCurrentPage(1);
  };

  const handleFetchDomains = () => {
    setDomainStatus(ACTIVE_STATUS.value);
    getDomainData(ACTIVE_STATUS.value);
    setSearchText("");
    setsearchedColumn("");
    setFilteredInfo({});
    setSortedInfo({});
    setCurrentPage(1);
  };

  const getColumnSearchProps = field => {
    let filterObj = {
      filterIcon: filtered => (
        <SearchOutlined
          id={`manage-account-search-btn-${field.key}`}
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
        />
      ),
    };
    if (field.type === "select") {
      if (!Utils.isEmpty(field.options)) {
        filterObj.filters = field.options.map(option => ({
          text: getMessage(option.label),
          value: option.value,
        }));
      }
      filterObj.filterIcon = filtered => (
        <FilterOutlined
          id={`manage-account-search-btn-${field.key}`}
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
        />
      );
      filterObj.onFilter = (value, record) => {
        const _value = value === "Active" ? true : false;
        return record[field.key] === _value;
      };
    } else {
      filterObj.filterDropdown = ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <SearchFilterDropdown
          setSelectedKeys={setSelectedKeys}
          selectedKeys={selectedKeys}
          confirm={confirm}
          clearFilters={clearFilters}
          handleSearch={handleSearch}
          dataIndex={field.key}
          searchInput={searchInput}
          handleReset={handleReset}
          field={field}
          label={getMessage(field.label)}
          inputId={`manage-domain-input-${field.key}`}
          resetBtnId={`manage-domain-reset-btn-${field.key}`}
          searchBtnId={`manage-domain-search-btn-${field.key}`}
          dataSource={domainsList}
        />
      );

      filterObj.onFilter = (value, record) => {
        return record[field.key]?.toString()?.toLowerCase().includes(value.toLowerCase());
      };
      filterObj.onFilterDropdownVisibleChange = visible => {
        if (visible) {
          setTimeout(() => searchInput && searchInput.select && searchInput.select());
        }
      };
    }
    return filterObj;
  };
  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    setSearchText(selectedKeys[0]);
    setsearchedColumn(dataIndex);
    confirm();
  };

  const handleReset = (clearFilters, confirm) => {
    setSearchText("");
    clearFilters();
    confirm();
  };

  const handleChange = (pagination, filters, sorter) => {
    const { current, pageSize } = pagination;
    setFilteredInfo(filters);
    setSortedInfo(sorter);
    setCurrentPageSize(pageSize);
    setCurrentPage(current);
  };

  const onEditDomain = record => {
    setShowAddDomainModal(true);
    setIsEdit(true);
    setEditDomainData({ ...record });
  };

  const domainColumns = Object.values(DOMAIN_TABLE_COLUMNS).map(field => {
    return {
      title: (
        <Tooltip title={getMessage(field.label)} placement="top">
          <span>{getMessage(field.label)}</span>
        </Tooltip>
      ),
      dataIndex: field.key,
      key: field.key,
      ellipsis: true,
      width: field.width || null,
      filteredValue: filteredInfo[field.key] || null,
      sorter: (a, b) => Utils.antdTableSorter(a, b, field.key),
      sortDirections: SORT_DIRECTIONS,
      sortOrder: sortedInfo.columnKey === field.key ? sortedInfo.order : null,
      ...getColumnSearchProps(field),
      render: (_, record) => {
        if (field.key === "isActive") {
          return record?.isActive === true
            ? getMessage(DOMAIN_FIELDS_NAME.ACTIVE.label)
            : getMessage(DOMAIN_FIELDS_NAME.IN_ACTIVE.label);
        }
        return (
          <Tooltip title={record[field.key]} placement="top">
            <span>{record[field.key]}</span>
          </Tooltip>
        );
      },
    };
  });

  if (hasAssetsRights(NAVIGATION_ASSETS.ManageDomains, NAVIGATION_ASSETS_RIGHTS.UPDATE)) {
    domainColumns.push({
      title: ACTION.text,
      key: ACTION.value,
      width: 80,
      render: (_, record) => (
        <Tooltip title={getMessage("common.label.edit")}>
          <Button onClick={() => onEditDomain(record)} size="small">
            <EditOutlined />
          </Button>
        </Tooltip>
      ),
    });
  }
  const handleUploadCancel = () => {
    setShowUploadModal(false);
  };
  return (
    <>
      <Row align="middle" gutter={[20, 20]} className="mb-2">
        <Col xs={24} md={12} lg={12} xl={12}>
          <PageHeader
            className="p-2 pl-2"
            title={MANAGE_DOMAINS_TITLE.text}
            subTitle={
              <Button className="ml-3" onClick={handleFetchDomains}>
                <SyncOutlined shape="circle" />
              </Button>
            }
          />
        </Col>
        <Col xs={24} md={12} lg={6} xl={6} className="pr-0 text-left text-md-left text-lg-left text-lg-right">
          <Radio.Group onChange={onChangeStatus} value={domainStatus}>
            <Radio value={ACTIVE_STATUS.value}>{ACTIVE_STATUS.text}</Radio>
            <Radio value={INACTIVE_STATUS.valueForProjects}>{INACTIVE_STATUS.text}</Radio>
            <Radio className="m-0" value={ALL_STATUS.value}>
              {ALL_STATUS.text}
            </Radio>
          </Radio.Group>
        </Col>
        <RestrictedContainer assetId={NAVIGATION_ASSETS.ManageDomains} assetRight={NAVIGATION_ASSETS_RIGHTS.CREATE}>
          <Col className="text-xl-right text-md-right" xs={24} md={12} lg={6} xl={6}>
            <Button type="primary" className="mr-3" onClick={Upload}>
              <PlusCircleOutlined className="mr-2" />
              {getMessage("common.upload.domain")}
            </Button>
          </Col>
        </RestrictedContainer>
        {/* <RestrictedContainer assetId={NAVIGATION_ASSETS.ManageDomains} assetRight={NAVIGATION_ASSETS_RIGHTS.CREATE}>
          <Col className="text-xl-right text-md-right" xs={24} md={12} lg={8} xl={8}>
            <Button type="primary" className="mr-3" onClick={AddDomain}>
              <PlusCircleOutlined className="mr-2" />
              {ADD_DOMAIN.text}
            </Button>
            <Button type="primary" className={`mr-3 ${isMobile && "mt-2"}`} onClick={AddSubDomain}>
              <PlusCircleOutlined className="mr-2" />
              {ADD_SUB_DOMAIN.text}
            </Button>
          </Col>
        </RestrictedContainer> */}
      </Row>
      <TableComponent
        rowKey={record => record.id}
        columns={domainColumns}
        data={domainsList}
        scroll={{ x: 1000 }}
        handleChange={handleChange}
        tableSize="small"
        showDropdown={false}
        showSorterTooltip={false}
        paginationHandler={{
          defaultPageSize: currentPageSize,
          current: currentPage,
        }}
      />
      {show && (
        <AddSubDomainForm
          setIsVisible={show}
          onCancel={() => setShow(false)}
          onSave={() => {
            getDomainData();
            dispatch(getSubDomains());
          }}
          domainsList={domainsList}
        />
      )}
      {showUploadModal && (
        <UploadData
          visible={showUploadModal}
          onCancel={handleUploadCancel}
          screen={SCREEN_ITEMS_TYPES.domain}
          sampleFile={domainsFile}
        />
      )}
      {showAddDomainModal && (
        <AddDomainForm
          visible={showAddDomainModal}
          onCancel={() => {
            setShowAddDomainModal(false);
            setEditDomainData(null);
          }}
          onSave={() => {
            getDomainData(domainStatus);
            setDomainStatus(ACTIVE_STATUS.value);
            dispatch(getSubDomains());
          }}
          editDomainData={editDomainData}
          isEdit={isEdit}
        />
      )}
    </>
  );
};

export default ManageDomains;
