import { Form, Input, message, Modal, Select } from "antd";
import {
  ADD_DOMAIN_FORM_NAME,
  ADD_DOMAIN_FORM_TITLE,
  DOMAIN_MESSAGES,
  FORM_FIELDS,
  UPDATE_DOMAIN_FORM_TITLE,
} from "constants/AddDomainFormConstant";
import {
  ADD_TEXT,
  CHILDREN,
  CONFIRM_MESSAGE,
  FORM_LAYOUT,
  SELECT_METHODS,
  VALIDATION_FAILED,
} from "constants/CommonConstants";
import { EMP_FORM_FIELDS } from "constants/ManageEmployeeConstants";
import useIntlMessage from "hooks/useIntlMessage";
import { useEffect, useState } from "react";
import { useIntl } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import domainService from "services/DomainService";
import { UPDATE_TEXT } from "../../../../constants/ManageAccounts";
import { STATUSES } from "../../../../constants/ManageEmployeeConstants";
import React from "react";
import { DESIGNATION_STATUSES } from "constants/ManageDesinations";
import { DESIGNATION_FORM_FIELDS } from "../../../../constants/ManageDesinations";

const AddDomain = ({ visible, onCancel, editDomainData, onSave }) => {
  const dispatch = useDispatch();
  const [form] = Form.useForm();
  const { Option } = Select;
  const { confirm } = Modal;
  const [subDomainList, setSubDomainList] = useState([]);
  const [errorMessage, setErrorMessage] = useState("");
  const [loading, setLoading] = useState(false);
  const intl = useIntl();
  const getMessage = useIntlMessage();
  const { SUB_DOMAIN, DOMAIN_NAME } = FORM_FIELDS;

  useEffect(() => {
    const getSubDomains = async domainID => {
      const list = await domainService.getSubDomainListV2(domainID);
      setSubDomainList(list);
    };

    if (editDomainData?.domainId) {
      getSubDomains(editDomainData.domainId);
    }
  }, [editDomainData]);

  const CreateDomain = async values => {
    try {
      setLoading(true);
      const saveResponse = await domainService.addDomainV2(values);
      verifySaveResult(saveResponse);
      form.resetFields();
      onSave();
      onCancel();
      message.success(intl.formatMessage({ id: DOMAIN_MESSAGES.DOMAIN_SUCCESS }));
    } catch (e) {
      console.error("Error in adding a new domain", e);
    } finally {
      setLoading(false);
    }
  };

  const UpdateDomain = async values => {
    try {
      if (editDomainData.isActive !== values?.domainStatus) {
        const saveResponse = await domainService.updateDomainStatus(values);
      }
      setLoading(true);
      const saveResponse = await domainService.updateDomainV2(values);
      verifySaveResult(saveResponse);
      form.resetFields();
      onSave();
      onCancel();
      message.success(intl.formatMessage({ id: DOMAIN_MESSAGES.DOMAIN_UPDATE }));
    } catch (e) {
    } finally {
      setLoading(false);
    }
  };

  const verifySaveResult = saveResponse => {
    if ([200, 201].includes(saveResponse?.statusCode)) {
      // If update or create request is successful
      return;
    }
    setErrorMessage(saveResponse?.description);
    throw new Error(saveResponse?.message);
  };

  const handleOk = () => {
    if (!form.isFieldsTouched()) {
      setErrorMessage(DOMAIN_MESSAGES.NO_CHANGES_TO_SAVE);
      return;
    }
    form
      .validateFields()
      .then(values => {
        if (editDomainData) {
          UpdateDomain({
            ...values,
            domainId: editDomainData?.domainId,
          });
        } else {
          CreateDomain({ domainName: values.domainName });
        }
      })
      .catch(info => {
        console.log(VALIDATION_FAILED.value, info);
      });
  };

  const handleConfirm = () => {
    confirm({
      title: CONFIRM_MESSAGE.value,
      centered: true,
      onOk() {
        onCancel();
      },
    });
  };

  const handleCancel = () => {
    if (form.isFieldsTouched()) {
      handleConfirm();
    } else {
      onCancel();
    }
  };

  const initialsData = {
    domainName: editDomainData?.domainName,
    subDomains: editDomainData?.subDomains,
    domainStatus: editDomainData?.isActive === true ? true : false,
  };
  console.log(editDomainData.subDomains);

  const isEditMode = React.useCallback(() => {
    return editDomainData && true;
  }, [editDomainData]);
  const formItemLayout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
  };
  return (
    <Modal
      visible={visible}
      title={editDomainData ? UPDATE_DOMAIN_FORM_TITLE : ADD_DOMAIN_FORM_TITLE}
      onCancel={handleCancel}
      onOk={handleOk}
      okText={editDomainData ? UPDATE_TEXT.text : ADD_TEXT.text}
      okButtonProps={{ loading: loading }}
    >
      <Form form={form} labelAlign="left" {...formItemLayout} name={ADD_DOMAIN_FORM_NAME} initialValues={initialsData}>
        <Form.Item
          name={DOMAIN_NAME.value}
          label={getMessage(DOMAIN_NAME.label)}
          rules={[
            {
              required: isEditMode() ? false : true,
            },
          ]}
        >
          {isEditMode() ? (
            <span className="profile-text-color">{editDomainData?.domainName}</span>
          ) : (
            <Input placeholder={getMessage(DOMAIN_NAME.placeholder)} />
          )}
        </Form.Item>
        {editDomainData && (
          <Form.Item
            name={SUB_DOMAIN.value}
            label={getMessage(SUB_DOMAIN.label)}
            rules={[
              {
                required: isEditMode() && editDomainData?.isActive !== true ? false : true,
                message: "Please select Sub Domain",
              },
            ]}
          >
            {isEditMode() && editDomainData?.isActive !== true ? (
              <span className="profile-text-color">{editDomainData?.subDomains?.map(x => x).join(", ")}</span>
            ) : (
              <Select
                // disabled={editDomainData?.isActive === true ? false : isEditMode()}
                placeholder={getMessage(SUB_DOMAIN.placeholder)}
                mode={SELECT_METHODS.MULTIPLE}
                showArrow
                showSearch
                optionFilterProp={CHILDREN}
              >
                {subDomainList?.map(item => {
                  return (
                    <Option key={item?.subDomainName} value={item?.subDomainName}>
                      {item?.subDomainName}
                    </Option>
                  );
                })}
              </Select>
            )}
          </Form.Item>
        )}

        <Form.Item
          name={FORM_FIELDS.DOMAIN_STATUS.name}
          label={getMessage(DESIGNATION_FORM_FIELDS.DESIGNATION_STATUS.label)}
          rules={[{ required: true }]}
        >
          <Select placeholder={getMessage(EMP_FORM_FIELDS.EMP_STATUS.label)} id="emp-status">
            {Object.values(DESIGNATION_STATUSES).map(e => {
              return (
                <Option key={e.name} value={e.value}>
                  {getMessage(e.name)}
                </Option>
              );
            })}
          </Select>
        </Form.Item>
        {errorMessage && <div style={{ color: "red" }}>* {errorMessage}</div>}
      </Form>
    </Modal>
  );
};

export default AddDomain;
